import React from 'react';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Home from "../Home/Home";
import Projects from "../Projects";
import './App.css';


export default function App() {
  return (
      <Router basename="/">
        <div>
          <div className="navbar navbar-expand navbar-default" role="navigation">
            <div className="container">
              <div id="nav">
                <ul className="navbar-nav navbar-center flex-wrap flex-row mx-auto justify-content-around">
                  <li className="nav-item px-2 px-sm-3 active"><a href="/">
                    ABOUT ME
                  </a></li>
                  <li className="nav-item px-2 px-sm-3"><a href="/#pubs">PUBLICATIONS</a></li>
                  <li className="nav-item px-2 px-sm-3"><Link to="/projects">PROJECTS</Link></li>
                  <li className="nav-item px-2 px-sm-3"><a href="/#other">OTHER</a></li>
                </ul>
              </div>
            </div>
          </div>
          <Switch>
            <Route exact path="/projects">
              <Projects />
            </Route>
            <Route exact path="/">
              <Home />
            </Route>
          </Switch>

          <footer>
            {/*<Contact />*/}
            paola.valdivia@inria.fr
            <br/>
            last update: Jan, 2021 &nbsp;&nbsp;&nbsp;&nbsp;
            ©  <a href="#about"> Paola Valdivia </a>
          </footer>
        </div>
      </Router>
  );
}



